@import 'variables.scss';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap'); 

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
header{
  width: 100%;
  height: 100px;
  
}

#navigation{
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu-item a{
  text-decoration: none;
  padding-right: 3em;
  
  color:black
}
.menu-item a:hover{
  color: $defaultColor;
}
#component-newsrails{
  height: 20px 300px; 
  font-weight: bolder;
  width: auto;
  background-color: $defaultColor;
  text-align: center;
}
.custom-logo{
  max-width: 100px;
}
